import React, { useState } from "react";
import { HeroGrid, HeroColumn } from "hero-react-grid";


const getRendererDate = (props) => {

    const date = props.value;
    const dd = String(date.getDate()).padStart(2, '0');
    const mm = String(date.getMonth() + 1).padStart(2, '0'); //January is 0!
    const yyyy = date.getFullYear();

    const today = mm + ' ' + dd + ' ' + yyyy;

    return (
        <div>
            {today}
        </div>
    )
};

function cellEditorSelector(params) {
    const fieldId = params.colDef.field;
    if (params.data[fieldId] instanceof Date && !isNaN(params.data[fieldId])) {
        return {
            component: 'agDateInput',
        };
    }

    return undefined;
}

function cellRendererSelector(params) {
    const fieldId = params.colDef.field;
    if (params.data[fieldId] instanceof Date && !isNaN(params.data[fieldId])) {
        return {
            component: 'agRendererDate'
        };
    }

    return undefined;
}

const SomeComponent = () => <div>I am a div</div>;
export default function App() {
    const [gridApi, setGridApi] = useState(null);
    const [gridColumnApi, setGridColumnApi] = useState(null);
    const [rowData, setRowData] = useState(null);

    const onGridReady = (params) => {
        setGridApi(params.api);
        setGridColumnApi(params.columnApi);

        const updateData = (data) => {
            setRowData(data.slice(0, 50));
        };

        fetch("https://www.ag-grid.com/example-assets/olympic-winners.json")
            .then((resp) => resp.json())
            .then((data) => updateData(data));
    };

    const filterParams = {
        comparator: function (filterLocalDateAtMidnight, cellValue) {
            var dateAsString = cellValue;
            if (dateAsString == null) return -1;
            var dateParts = dateAsString.split('/');
            var cellDate = new Date(
                Number(dateParts[2]),
                Number(dateParts[1]) - 1,
                Number(dateParts[0])
            );
            if (filterLocalDateAtMidnight.getTime() === cellDate.getTime()) {
                return 0;
            }
            if (cellDate < filterLocalDateAtMidnight) {
                return -1;
            }
            if (cellDate > filterLocalDateAtMidnight) {
                return 1;
            }
        },
        browserDatePicker: true,
    };

    return (
        <div className="App">
            <HeroGrid
                rowData={rowData}
                widthGrid="100%"
                defaultColDef={{
                    flex: 1,
                    minWidth: 150,
                    filter: true,
                    filterParams: {
                        buttons: ['reset', 'apply']
                    }
                }}
                heightGrid="400px"
                addRowButton
                allowExport
                allowImport
                actionsComponent={<SomeComponent />}
                components={{
                    agDateInput: getDatePicker(),
                }}
                frameworkComponents={{
                    agRendererDate: getRendererDate
                }}

                onGridReady={onGridReady}
            >
                <HeroColumn field="country" sortable  editable tempData={['United States', 'Australia', 'Canada']} />
                <HeroColumn field="athlete" minWidth={180} tempData={['Alex']} />
                <HeroColumn field="age" filter="agNumberColumnFilter" />
                <HeroColumn field="year" />
                <HeroColumn field="date"
                            editable
                            minWidth={150}
                            filter="agDateColumnFilter"
                            filterParams={filterParams}
                            cellRendererSelector={cellRendererSelector}
                            cellEditorSelector={cellEditorSelector}
                            tempData={[ new Date(), '12/12/2012']}
                />
                <HeroColumn field="sport" minWidth={150} />
                <HeroColumn field="gold" />
                <HeroColumn field="silver" />
                <HeroColumn field="bronze" />
                <HeroColumn field="total" />
            </HeroGrid>
        </div>
    );
}

function getDatePicker() {
    function Datepicker() {}
    Datepicker.prototype.init = function (params) {

        this.eInput = document.createElement('input');
        this.eInput.value = new Date(params.value);
        this.eInput.type = 'date';
        this.eInput.classList.add('ag-input');
        this.eInput.style.height = '100%';
        console.log(this.eInput)
        // $(this.eInput).datepicker({ dateFormat: 'dd/mm/yy' });
    };
    Datepicker.prototype.getGui = function () {
        return this.eInput;
    };
    Datepicker.prototype.afterGuiAttached = function () {
        console.log('afterGuiAttached', this.eInput)
        this.eInput.focus();
        this.eInput.select();
    };
    Datepicker.prototype.getValue = function () {
        return this.eInput.value;
    };
    Datepicker.prototype.destroy = () => {};
    Datepicker.prototype.isPopup = () => {
        return false;
    };
    return Datepicker;
}
